import React, { useState } from "react";
import Page1 from "./Page1.jsx";
import Page2 from "./Page2.jsx";
import Page3 from "./Page3.jsx";

function App() {
  const [page, setPage] = useState(1);

  const [formData, setFormData] = useState({
    hourlyReadings: [],
    issues: [],
    productionData: [],
    date: "",
    factory: "",
    machineNumber: "",
    productType: "",
    shift: "",
    teamLead: "",
    supervisor: "",
    comments: "",
  });

  const handleChange = (field, value) => {
    setFormData(prev => ({
      ...prev,
      [field]: value,
    }));
  };

  const nextPage = () => setPage(prev => prev + 1);
  const prevPage = () => setPage(prev => prev - 1);

  return (
    <div className="p-4">
      {page === 1 && <Page1 data={formData} onChange={handleChange} />}
      {page === 2 && <Page2 data={formData} onChange={handleChange} />}
      {page === 3 && <Page3 data={formData} onChange={handleChange} />}

      <div className="flex justify-between mt-6">
        {page > 1 && (
          <button onClick={prevPage} className="bg-gray-400 text-white px-4 py-2 rounded">
            Previous
          </button>
        )}
        {page < 3 && (
          <button onClick={nextPage} className="bg-blue-600 text-white px-4 py-2 rounded">
            Next
          </button>
        )}
      </div>
    </div>
  );
}

export default App;
