import React from "react";

export default function Page1({ data, onChange }) {
  if (!data) return <div>Loading...</div>;

  return (
    <div className="space-y-6">
      <h2 className="text-xl font-semibold">Page 1: Machine Report Info</h2>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <label className="block text-sm font-medium">Date</label>
          <input
            type="date"
            value={data.date || ""}
            onChange={e => onChange("date", e.target.value)}
            className="w-full border p-2 rounded"
          />
        </div>

        <div>
          <label className="block text-sm font-medium">Factory</label>
          <input
            type="text"
            value={data.factory || ""}
            onChange={e => onChange("factory", e.target.value)}
            className="w-full border p-2 rounded"
          />
        </div>

        <div>
          <label className="block text-sm font-medium">Machine Number</label>
          <input
            type="text"
            value={data.machineNumber || ""}
            onChange={e => onChange("machineNumber", e.target.value)}
            className="w-full border p-2 rounded"
          />
        </div>

        <div>
          <label className="block text-sm font-medium">Product Type</label>
          <input
            type="text"
            value={data.productType || ""}
            onChange={e => onChange("productType", e.target.value)}
            className="w-full border p-2 rounded"
          />
        </div>

        <div>
          <label className="block text-sm font-medium">Shift</label>
          <input
            type="text"
            value={data.shift || ""}
            onChange={e => onChange("shift", e.target.value)}
            className="w-full border p-2 rounded"
          />
        </div>
      </div>
    </div>
  );
}
