import React from "react";

export default function Page2({ data, onChange }) {
  const handleTableChange = (section, idx, field, value) => {
    const updatedSection = [...(data[section] || [])];
    if (!updatedSection[idx]) updatedSection[idx] = {};
    updatedSection[idx][field] = value;
    onChange(section, updatedSection);
  };

  return (
    <div className="container space-y-4">
      <h2 className="text-xl font-semibold">Page 2: Additional Report Data</h2>

      <h3 className="text-lg font-medium">Production Data</h3>
      <table className="w-full table-auto border border-gray-300">
        <thead>
          <tr className="bg-gray-100">
            <th className="border p-2">Time</th>
            <th className="border p-2">Output (Kg)</th>
            <th className="border p-2">Rejects (Kg)</th>
            <th className="border p-2">Reason for Reject</th>
          </tr>
        </thead>
        <tbody>
          {[...Array(12)].map((_, idx) => (
            <tr key={idx}>
              <td className="border p-2">{`Hour ${idx + 1}`}</td>
              <td className="border p-2">
                <input
                  type="text"
                  value={data.productionData?.[idx]?.output || ""}
                  onChange={e =>
                    handleTableChange("productionData", idx, "output", e.target.value)
                  }
                  className="w-full border p-1 rounded"
                />
              </td>
              <td className="border p-2">
                <input
                  type="text"
                  value={data.productionData?.[idx]?.rejects || ""}
                  onChange={e =>
                    handleTableChange("productionData", idx, "rejects", e.target.value)
                  }
                  className="w-full border p-1 rounded"
                />
              </td>
              <td className="border p-2">
                <input
                  type="text"
                  value={data.productionData?.[idx]?.rejectReason || ""}
                  onChange={e =>
                    handleTableChange("productionData", idx, "rejectReason", e.target.value)
                  }
                  className="w-full border p-1 rounded"
                />
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
